/**
 * Interface configuration parameters used across the app.
 */

export const BREAKPOINT_SMALL = 768;

export const isSmallScreen = () => global.innerWidth <= BREAKPOINT_SMALL;

export const CHART_ASPECT_RATIO = 3 / 2;
export const MINIMUM_CHART_MARGIN = 10;



